import React from "react";
import "./styles.css";
import productImg from "./assets/product-img.webp";

const ProductCard = () => {
  return (
    <div className="card">
      <img src={productImg} className="card-img-top" />
      <div className="card-body">
        
      </div>
    </div>
  );
};

export default ProductCard;
